import axios from "axios";
import React, { useEffect, useState } from "react";
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  CardText,
  CardTitle,
  Col,
  Row,
} from "reactstrap";
import "../css/details.css";
const Details = () => {
  const [submissions, setSubmissions] = useState([]);
  const fetchDetails = async () => {
    const response = await axios.get("https://stack-fusion-task.onrender.com/api/submissions");
    response.data.success && setSubmissions(response.data.data);
  };
  useEffect(()=>{
    fetchDetails()
  },[])
  return (
    <>
      <h1 style={{ marginTop: "50px" }}>
        Here is the all submitted form List......
      </h1>
      <Row md={3}>
        {submissions.map((item,index) => {
          return (
            <Col key={index}>
              <Card>
                <CardHeader>Submission #{index+1}</CardHeader>
                <CardBody>
                  <CardText>Name: {item.name}<br/>Email: {item.email}<br/>DOB: {item.dob}<br/>Mobile Number: {item.mobile_number}</CardText>
                  
                </CardBody>
              </Card>
            </Col>
          );
        })}
      </Row>
     
    </>
  );
};

export default Details;
