import axios from 'axios';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
const Form = () => {
	const [name,setName]=useState("");
	const [email,setEmail]=useState("");
	const [mobile_number,setMobile]=useState();
	const [dob,setDob]=useState("");
	const navigate = useNavigate()
	const submitForm =async (e)=>{
		
		e.preventDefault()
		const date = new Date(dob);
		const currentDate = new Date()
		if( (currentDate.getFullYear()-date.getFullYear())<18){
			console.log(date.getFullYear(),currentDate.getFullYear())
			return alert("Age Must be Above 18!")
		}
		
		const response = await axios.post("https://stack-fusion-task.onrender.com/api/submit-form",{
			name:name.trim(),
			email:email,
			mobile_number:Number(mobile_number),
			dob:dob
		})
		console.log(response.data)
		response.data.success && navigate("/submissions")
		if(!response.data.success){

			// document.forms[0].classList.add("was-validated")
		}
	}
  return (
    <> 
     
     <div className="container">
	<div className="screen">
		<div className="screen__content">
			
            <form className="login need-validation" onSubmit={e=>{submitForm(e)}}>
           
				<div className="field">
					<input type="text" className="input" placeholder="Name" value={name} onChange={(e)=>{
						setName(e.target.value.trimStart())
					}} required/>
				</div>
				<div className="field">
					<input type="date" className="input" placeholder="DOB" value={dob} onChange={e=>setDob(e.target.value)} required/>
				</div>
				<div className="field">
					<input type="email" required className="input" placeholder="Email" value={email} onChange={e=>setEmail(e.target.value)}/>
				</div>
				<div className="field">
					<input type="tel" className="input" placeholder="Mobile Number" maxLength="10" value={mobile_number} onChange={e=>setMobile(e.target.value)} required/>
				</div>
				<button type='submit'  className="button submit">
					<span className="button__text"> Submit</span>
				</button>				
			</form>
			
		</div>
		<div className="screen__background">
			<span className="screen__background__shape screen__background__shape4"></span>
			<span className="screen__background__shape screen__background__shape3"></span>		
			<span className="screen__background__shape screen__background__shape2"></span>
			<span className="screen__background__shape screen__background__shape1"></span>
		</div>		
	</div>
</div>

    </>
  )
}

export default Form;
